.block-three-cols {
  width: 100%;
  padding: 50px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(var(--primary-color), 1);
}
@media (min-width: 1024px) {
  .block-three-cols {
    padding: 120px 0;
  }
}
.block-three-cols .block__inner {
  max-width: 1800px;
  width: 95%;
  margin: 0 auto;
  display: flex;
}
.block-three-cols .cols {
  width: 100%;
  height: 100%;
  padding: 0 50px;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  column-gap: 3rem;
  row-gap: 2.5rem;
}
@media (max-width: 1024px) {
  .block-three-cols .cols {
    display: flex;
    flex-direction: column;
    gap: 3rem;
  }
}
.block-three-cols .cols .col {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.block-three-cols .cols .col:has(.image) {
  grid-row: 1/3;
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.block-three-cols .cols .col .block-title {
  font-family: var(--adore);
  font-size: 8.2rem;
  font-weight: 700;
  line-height: 1.2;
  text-transform: uppercase;
  animation: 2s ease 0s 1 fadeIn;
}
@media (max-width: 1024px) {
  .block-three-cols .cols .col .block-title {
    font-size: 36px;
  }
}
.block-three-cols .cols .col .heading {
  margin-bottom: 1rem;
  font-family: var(--montserrat);
  font-size: 2.2rem;
  font-weight: 700;
  animation: 2s ease 0s 1 fadeIn;
}
.block-three-cols .cols .col .content {
  animation: 2s ease 0s 1 fadeIn;
}
.block-three-cols .cols .col .content p,
.block-three-cols .cols .col .content span,
.block-three-cols .cols .col .content li,
.block-three-cols .cols .col .content a {
  font-size: 1.6rem;
}
.block-three-cols .cols .col .image {
  max-width: 720px;
  width: 100%;
  height: 800px;
  max-height: 100%;
  position: relative;
  display: flex;
  overflow: hidden;
  clip-path: inset(0);
  background-color: #000;
}
@media (max-width: 1024px) {
  .block-three-cols .cols .col .image {
    height: 220px;
  }
}
@keyframes slideInFromLeft {
  0% {
    opacity: 0;
    left: 100%;
  }
  100% {
    opacity: 1;
    left: 50%;
  }
}
.block-three-cols .cols .col .image img {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  animation: 2s ease 0s 1 slideInFromLeft;
  object-fit: cover;
}
.block-three-cols .cols .col .list {
  display: flex;
  flex-direction: column;
  list-style: none;
}
.block-three-cols .cols .col .list li {
  width: 100%;
  display: flex;
  font-size: 1.6rem;
  font-weight: 600;
  border-bottom: 1px solid rgba(var(--text-color), 1);
  animation: 2s ease 0s 1 fadeIn;
}
.block-three-cols .cols.tic .col:first-child {
  grid-column: 1/4;
}
.block-three-cols .cols.tic .col:nth-child(2) {
  grid-column: 4/9;
  align-items: center;
}
.block-three-cols .cols.tic .col:nth-child(3) {
  grid-column: 9/11;
  grid-row: 1/2;
}
.block-three-cols .cols.tic .col:last-child {
  grid-column: 9/11;
  grid-row: 2/3;
}
@media (max-width: 1024px) {
  .block-three-cols .cols.tic .col:nth-child(2) {
    padding-right: 0;
  }
}
.block-three-cols .cols.tci .image {
  height: 550px;
}
@media (max-width: 1024px) {
  .block-three-cols .cols.tci .image {
    height: 220px;
  }
}
.block-three-cols .cols.tci .col:first-child {
  grid-column: 1/3;
}
.block-three-cols .cols.tci .col:nth-child(2) {
  grid-column: 8/11;
  grid-row: 1/2;
}
.block-three-cols .cols.tci .col:nth-child(3) {
  grid-column: 4/7;
}
.block-three-cols .cols.tci .col:last-child {
  grid-column: 8/11;
  grid-row: 2/3;
}
@media (max-width: 1024px) {
  .block-three-cols .cols.tci .col:first-child {
    order: 0;
  }
  .block-three-cols .cols.tci .col:nth-child(2) {
    order: 2;
  }
  .block-three-cols .cols.tci .col:nth-child(3) {
    order: 1;
  }
  .block-three-cols .cols.tci .col:last-child {
    order: 4;
  }
}
.block-three-cols .cols.cit {
  column-gap: 5rem;
}
.block-three-cols .cols.cit .image {
  min-height: 800px;
}
@media (max-width: 1024px) {
  .block-three-cols .cols.cit .image {
    min-height: 220px;
  }
}
.block-three-cols .cols.cit .col:first-child {
  grid-column: 8/11;
  grid-row: 1/2;
}
.block-three-cols .cols.cit .col:nth-child(2) {
  grid-column: 4/8;
  grid-row: 1/10;
}
.block-three-cols .cols.cit .col:nth-child(3) {
  grid-column: 1/4;
  grid-row: 1/10;
}
.block-three-cols .cols.cit .col:last-child {
  grid-column: 8/11;
  grid-row: 2/10;
}
@media (max-width: 1024px) {
  .block-three-cols .cols.cit .col:first-child {
    order: 0;
  }
  .block-three-cols .cols.cit .col:nth-child(2) {
    order: 2;
  }
  .block-three-cols .cols.cit .col:nth-child(3) {
    order: 3;
  }
  .block-three-cols .cols.cit .col:last-child {
    order: 1;
  }
}